              ul {
                li {
                  font-size: rem(17px);
                }
              }
              .btn {
                left: 70%;
              }
            }
          }
        }
      }
    }
  }
  //VISA AMERICA
  .visa_america {
    .visa_america_wrap {
      .america_content {
        .container_visa {
          .btn_form {
            -webkit-transition: top .4s .3s;
            transition: top .4s .3s;
            &.active{
              top: rem(-30px);
            }
          }
          .america_form {
            padding: rem(60px) rem(30px);
            left: auto;
          }
        }
      }
    }
  }
  //MAIN SLIDE
  .main_slide {
    .main_slide_wrap {
      .main_slide_elem {
        .main_slide_text {
          .main_title {
            font-size: rem(30px);
            line-height: 1;
          }
          .title_item {
            font-size: rem(20px);
          }
        }
      }
    }
  }
  //FRANCE DAY
  .france_day_content {
    .container {
      .france_day_img {
        .day_img_elem {
          max-width: none;
          -webkit-box-flex: 1;
              -ms-flex: 1 1 100%;
                  flex: 1 1 100%;
          margin-bottom: rem(5px);
        }
      }
    }
  }
  //CONTACTS
  .contacts_wrap {
    padding: rem(15px);
    -ms-flex-item-align: end;
        align-self: flex-end;
    .contacts_content {
      .contacts_info {
        padding: 0 rem(15px);
      }
      .social {
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
        margin-left: 0;
        .btn {
          margin-top: rem(15px);
          margin-left: 0;
        }
      }
    }
  }
  //CONTACTS CONTENT WORKERS
  .contacts_workers {
    .container {
      .workers_content {
        .workers_elem {
          padding: 0;
          margin-bottom: rem(30px);
        }

      }
    }
  }
  //EUROPE SECTION
  .europe_section {
    .container {
      .region_content {
        .select_region {
          .region_content_wrap {
            .region_content_elem {
              h3 {
                font-size: rem(18px);
              }
              .region_list {
                ul {
                  li {
                    max-width: 100%;
                    -webkit-box-flex: 1;
                        -ms-flex: 1 1 100%;
                            flex: 1 1 100%;
                  }
                }
              }
            }
          }
        }
      }
      .slide_europe {
        .slide_europe_content {
          .slide_europe_text {
            h2 {
              font-size: rem(30px);
            }
          }
        }
      }
    }
  }
  //INDIVIDUAL TOURS
  .hotel_booking {
    .container {
      .hotel_booking_wrap {
        .hotel_booking_elem {
          .booking_content {
            h2 {
              font-size: rem(20px);
            }
            p {
              line-height: 1.5;
              font-size: rem(16px);
            }
          }
        }
      }
    }
  }
  //CONCRETE CONTENT
  .concrete_content {
    .concrete_img {
      .concrete_img_elem {
        max-width: none;
        width: 100%;
        -webkit-box-flex: 1;
            -ms-flex: 1 1 100%;
                flex: 1 1 100%;
        margin-right: auto;
        &:nth-child(3n+3) {
          margin-right: auto;
        }
      }
    }
  }
  //MENU
  .menu {

    .container {
      .menu_wrap {
        .left_menu {
          .menu_elem {
            li {

              a {
                font-size: rem(12px);
              }
            }
            .menu_open {
              .menu_right_btn {
                font-size: rem(12px);
              }
            }
          }
        }
        .right_menu {
          .right_menu_content {
            .right_menu_text {
              font-size: rem(15px);
            }
            p {
              font-size: rem(12px);
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 470px) {
  //VISA SECTION
  .visa_section {
    .visa_usa_wrap {
      .visa_usa {
        padding: rem(10px);
        h1 {
          font-size: rem(25px);
        }
        h2 {
          font-size: rem(15px);
        }
        .consultation_visa {

          margin-bottom: rem(5px);
          p {
            font-size: rem(15px);
          }

        }
      }
      .visa_usa_info {

        .visa_usa_form {
          ul {
            li {
              font-size: rem(15px);
            }
          }
        }
      }
    }
  }
}

@media screen and (min-width: 1280px) {
  //MENU
  .menu {
    .container {
      .menu_wrap {
        .left_menu {
          .menu_elem {
            .menu_open {
              &:hover {
                .menu_right_btn {
                  color: $orange_color;
                }
                .menu_drop {
                  display: block;
                  visibility: visible;
                  opacity: 1;
                  -webkit-transform: scale(1);
                          transform: scale(1);
                }
              }
              .menu_drop {
                position: absolute;
                top: 0;
                left: 100%;
                visibility: hidden;
                opacity: 0;
                width: 100%;
                display: -webkit-box;
                display: -ms-flexbox;
                display: flex;
                -webkit-box-orient: vertical;
                -webkit-box-direction: normal;
                    -ms-flex-direction: column;
                        flex-direction: column;
                -webkit-transform: scale(.1);
                        transform: scale(.1);
                -webkit-transition: .4s;
                transition: .4s;
              }
            }
          }
        }
      }
    }
  }
  //TOP SECTION
  .top_section {
    .container {
      .top_section_wrap {
        .top_content {
          -webkit-transition: .4s;
          transition: .4s;
          &:hover {
            -webkit-transition: .4s;
            transition: .4s;
            .content {
              bottom: 0;
              -webkit-transition: bottom .4s;
              transition: bottom .4s;
              .content_elem {
                .line_home {
                  left: 0;
                  span {
                    visibility: visible;
                    opacity: 1;
                  }
                }
                .btn {
                  left: 80%;
                  opacity: 1;
                }
              }
            }
          }
          img {
            width: 100%;
            height: 100%;
            -o-object-fit: cover;
               object-fit: cover;
          }
          .content {
            position: absolute;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            padding: rem(50px) rem(70px);
            width: 100%;
            height: 100%;
            bottom: -63%;
            -webkit-transition: bottom .4s;
            transition: bottom .4s;
            .content_elem {
              .line_home {
                -webkit-transition-delay: .1s;
                        transition-delay: .1s;
                left: calc(50% - 25px);
                margin-bottom: rem(15px);
                -webkit-transition: .4s;
                transition: .4s;
                span {
                  visibility: hidden;
                  -webkit-transition: .4s;
                  transition: .4s;
                  opacity: 0;
                }
              }
              .btn {
                position: absolute;
                left: 30%;
                bottom: 10%;
                max-width: rem(76px);
                border: 1px solid $orange_color;
                font-size: rem(18px);
                //margin-bottom: rem(30px);
                opacity: 0;
                -webkit-transition: color .4s, opacity .4s .4s, left .4s .4s, background-color .4s;
                transition: color .4s, opacity .4s .4s, left .4s .4s, background-color .4s;
              }
            }
          }
        }
      }
    }
  }
}



